.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

h4, h5, h6 {
  margin: 0.3rem auto;
}

.flex-container {
  margin: 0px auto;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
img{
  margin:0px;
}
.photo-img {
  margin: 5px;
  height: 250px;
  width: 250px;
}

.photo-img-home {
  margin: 10px 50px 10px 50px;
  height: 250px;
  width: 250px;
}
 
.photo-entry-block {
  padding: 10px;
  margin: 5px;
  width: 80%;
}
 
.row-header{
  margin:0px auto;
  padding:0px auto;
}

.row-header{
  margin:0px auto;
  padding:0px auto;
}

.row-content {
  margin:0px auto;
  padding: 50px 0px 50px 0px;
  border-bottom: 1px ridge;
  min-height:400px;
}

.footer{
  background-color: #adb5bd;
  margin:0px auto;
  padding: 20px 0px 20px 0px;
}
.jumbotron {
  padding:50px 30px 50px 30px;
  margin:0px auto;
  background: #8d99ae;
  color:floralwhite;
}

address{
  font-size:80%;
  margin:0px;
  color:#0f0f0f;
}

.navbar-dark {
  background-color: #2b2d42;
}


@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}



